



































import uniIcon from '../../components/uni-icon.vue'

export default {
	data() {
		return {
			items: [{
					name: '1',
					value: '欺诈',
					checked: false
				},
				{
					name: '2',
					value: '色情',
					checked: true
				},
				{
					name: '3',
					value: '政治谣言',
					checked: false
				},
				{
					name: '4',
					value: '内容侵权',
					checked: false
				}
			]
		};
	},
	components: {
		uniIcon
	},
	methods: {
		selectValue: function(name) {
			console.log(name);
			this.items.forEach(item => {
				if(item.name == name){
					item.checked = true
				}else{
					item.checked = false
				}
			})
		}
	}
}



